import { Component, OnInit, Input} from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { DataService } from '../../services/data.service';
import { Router } from '@angular/router';
 
@Component({
  selector: 'app-page3',
  templateUrl: './page3.component.html',
  styleUrls: ['./page3.component.css'],
   
})
export class Page3Component implements OnInit {
@Input() choice:number=0;
 mycomps:string[];
 private id;
 sub;
  constructor(private route: ActivatedRoute,private router: Router,private ds:DataService) {

    this.mycomps=['Photoshop','Illustrator','Indesign','Animate','XD'];
    
 
     
  }

  ngOnInit() {
    this.sub = this.route.params.subscribe(params => {
      this.id = +params['id']; // (+) converts string 'id' to a number
    });
    
  }
  
  private ngOnDestroy() {
    this.sub.unsubscribe();
  }
onClick (i) {
         
    this.router.navigate(['/works',i,this.mycomps[i]]);
  }


}



 


